* {
  box-sizing: border-box;
}
body {
  margin: 20px;
  padding: 0;
  font-family: sans-serif;
  background: linear-gradient(0deg, #d9cfe0, #e7e1ee, #d9cfe0);
}

.clear {
  clear: both;
  content: "";
}

.game {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background: #48E5C2;
  margin-top: 15vh;
}
.game-info {
  margin-bottom: 20px;
  background: transparent;
  padding: 7px;
  text-align: center;
  color: #fff;
  min-height: 100px;
  border-radius: 7px;
}

.info {
  display: block;
  margin-top: 15px;
}
h1,p {
  margin: 0;
}

.cell {
  background: #fff;
  border: 1px solid #48E5C2;
  border-radius: 4px;
  float: left;
  line-height: 45px;
  height: 45px;
  text-align: center;
  width: 45px;
  min-width: 45px;
  cursor: pointer;
  color: #333333;
  font-weight: 600;
}

.cell:focus {
  outline: none;
}

.hidden {
  background: #333333;
}

.is-flag,
.is-mine {
  color: #fc543c;
}
